// lib/firebase.ts
import { initializeApp } from "firebase/app";
import { getAuth, GoogleAuthProvider, EmailAuthProvider } from "firebase/auth";
import { getFirestore, connectFirestoreEmulator } from "firebase/firestore";
import { getStorage } from "firebase/storage";
import { getFunctions, connectFunctionsEmulator } from "firebase/functions";
import { getAnalytics, isSupported } from "firebase/analytics";
import { getPerformance } from "firebase/performance";

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyAZgykutTrEf_s4X53uL0e5g5IeU9LN7Ao",
  authDomain: "paddle-f3c68.firebaseapp.com",
  projectId: "paddle-f3c68",
  storageBucket: "paddle-f3c68.firebasestorage.app",
  messagingSenderId: "226043314594",
  appId: "1:226043314594:web:95d919cb8c5ded72ebf2fb",
  measurementId: "G-1R6HX54FVL"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Authentication
export const auth = getAuth(app);
auth.useDeviceLanguage(); // For better auth flows

// Providers
export const googleProvider = new GoogleAuthProvider();
googleProvider.setCustomParameters({ prompt: 'select_account' }); // Better UX for Google Auth

export const emailProvider = new EmailAuthProvider();

// Firestore Database
export const firestore = getFirestore(app);

// Cloud Storage
export const storage = getStorage(app);

// Cloud Functions
export const functions = getFunctions(app);

// Analytics (only initialize in client-side)
export const analytics = typeof window !== 'undefined' && isSupported() ? getAnalytics(app) : null;

// Performance Monitoring
export const performance = typeof window !== 'undefined' ? getPerformance(app) : null;

// Emulator setup (for development)
if (process.env.NODE_ENV === 'development') {
  connectFirestoreEmulator(firestore, 'localhost', 8080);
  connectFunctionsEmulator(functions, 'localhost', 5001);
  // Note: Auth emulator needs to be set up in your auth flows
}

// Utility function to get current user token
export const getCurrentUserToken = async (): Promise<string | null> => {
  try {
    const user = auth.currentUser;
    if (!user) return null;
    return await user.getIdToken();
  } catch (error) {
    console.error('Error getting user token:', error);
    return null;
  }
};